
































// @ is an alias to /src
import Vue from 'vue'
import Page from '@/components/Page.vue'
const IconForm = require('./../assets/icon_nav_form.png')
const IconLayout = require('./../assets/icon_nav_layout.png')
const IconFeedback = require('./../assets/icon_nav_feedback.png')
const IconNav = require('./../assets/icon_nav_nav.png')
const IconSearch = require('./../assets/icon_nav_search.png')

export default Vue.extend({
  name: 'Home',
  data() {
    return {
      selected: -1,
      menuList: [
        {
          name: '表单',
          icon: IconForm,
          children: [
            { title: 'Button', url: '/button' },
            { title: 'Form', url: '/form' },
            { title: 'List', url: '/list' },
            { title: 'Slider', url: '/slider' },
            { title: 'Uploader', url: '/uploader' },
          ],
        },
        {
          name: '基础组件',
          icon: IconLayout,
          children: [
            { title: 'Article', url: '/article' },
            { title: 'Badge', url: '/badge' },
            { title: 'Flex', url: '/flex' },
            { title: 'Footer', url: '/footer' },
            { title: 'Gallery', url: '/gallery' },
            { title: 'Grid', url: '/grid' },
            { title: 'Icons', url: '/icons' },
            { title: 'Loading', url: '/loading' },
            { title: 'Loadmore', url: '/loadmore' },
            { title: 'Panel', url: '/panel' },
            { title: 'Preview', url: '/preview' },
            { title: 'Progress', url: '/progress' },
          ],
        },
        {
          name: '操作反馈',
          icon: IconFeedback,
          children: [
            { title: 'ActionSheet', url: '/action-sheet' },
            { title: 'Dialog', url: '/dialog' },
            { title: 'Half-screen Dialog', url: '/half-screen-dialog' },
            { title: 'Msg', url: '/msg' },
            { title: 'Picker', url: '/picker' },
            { title: 'Toast', url: '/toast' },
            { title: 'TopTips', url: '/toptips' },
          ],
        },
        {
          name: '导航相关',
          icon: IconNav,
          children: [
            { title: 'Navbar', url: '/navbar' },
            { title: 'Tabbar', url: '/tabbar' },
          ],
        },
        {
          name: '搜索相关',
          icon: IconSearch,
          children: [{ title: 'Search Bar', url: '/search-bar' }],
        },
      ],
    }
  },
  components: {
    Page,
  },
  methods: {
    goUrl(url: string): void {
      this.$router.push(url)
    },
    changeMenu(index: number): void {
      if (this.selected != index) {
        this.selected = index
      } else {
        this.selected = -1
      }
    },
  },
})
